.why {
    @include container;
    padding-block: 72px;

    &__text {
        margin-top: 24px;
        font-family: "Ubuntu";
        font-size: 24px;
        font-weight: 300;
        line-height: 120%;
        color: $text;
        max-width: 630px;
        margin-bottom: 128px;
    }

    &__pictures {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 30px;

        img {
            width: 300px;
            height: 420px;
            border-radius: 32px;
            
            &:nth-of-type(2) {
                margin-top: -100px;
            }
            
            &:nth-of-type(3) {
                margin-top: -200px;
            }
            
            &:nth-of-type(4) {
                margin-top: -300px;
            }
        }
    }
}
