$fonts: ('Montserrat', 'Regular', 400),
        ('Montserrat', 'Medium', 500),
        ('Montserrat', 'SemiBold', 600),
        ('Comfortaa', 'Regular', 400),
        ('Comfortaa', 'SemiBold', 600),
        ('Comfortaa', 'Bold', 700),
        ('Ubuntu', 'Light', 300),
        ('Ubuntu', 'Regular', 400),
        ('Ubuntu', 'Medium', 500),
        ('Ubuntu', 'Bold', 700),
        ('FontAwesome', 300, 300),
        ('FontAwesome', 900, 900),
        ('FontAwesome', 'Brands', 500);

@each $name, $nameW, $w in $fonts {
    @font-face {
        font-family: $name;
        src: url('../fonts/#{$name}/#{$name}-#{$nameW}.woff2');
        font-weight: $w;
    }
}


$main: #fafafa;
$extra: #7E64F9;
$extra_hover: #9D66E4;
$main-gradient: linear-gradient(90deg, #8D75FF 0%, #4C1FAF 100%);
$promo-gradient: linear-gradient(90deg, #6B59C1 0%, #5D2CCA 31%, #8F0ACE 62%, #755DC1 100%);
$promo-gradient_hover: linear-gradient(90deg, #8F0ACE 15%, #5D2CCA 33%, #755DC1 68%, #6B59C1 100%);
$text: #292929;
$text_hover: #646464;
$text_active: #46464690;
$light-price: #7524dca6;
$light-button: #5c00d199;
$nav-bg: #eeeeee;
$main-shadow: 0px 4px 10px 0px #8d75ffcc;
$extra-shadow: 0px 4px 10px 0px #584a6fcc;
$text-extra: #FFFFFF;
$text-extra_hover: #ffffffd0;
$stars: #FAFF00;
$stars-stroke: #D9D000;
$tours-overlay: #32005140;
$types-overlay: #19002940;
$stats-overlay: linear-gradient(90deg, #694aff80 0%, #27007d80 100%);