.services {
    @include container;

    &__abilities {
        padding-block: 32px 72px;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 30px;

        &-item {
            background: $nav-bg;
            border-radius: 32px;

            padding: 48px 30px;

            display: flex;
            flex-direction: column;
            align-items: center;

            box-shadow: $extra-shadow;
            transition: .2s;

            &:hover {
                transform: scale(.98);
            }
        }

        &-img {
            border-radius: 50%;
            background: $main-gradient;
            width: 125px;
            height: 125px;
            display: flex;
            align-items: center;
            justify-content: center;

            img {
                width: 72px;
                height: auto;
            }
        }

        &-title {
            font-family: "Ubuntu";
            color: $text;
            font-size: 32px;
            font-weight: 400;
            margin-block: 16px;
            text-align: center;
        }

        &-text {
            text-align: right;
            font-family: "Ubuntu";
            color: $text;
            font-size: 20px;
            font-weight: 300;
        }
    }
}
