.types {
    @include container;
    padding-block: 72px;

    &__content {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 30px;

        margin-top: 48px;
    }

    &__card {
        height: 400px;
        width: 100%;
        position: relative;
        border-radius: 16px;
        overflow: hidden;
        z-index: 1;
        display: flex;
        align-items: center;
        justify-content: center;

        font-family: "Comfortaa";
        font-size: 32px;
        font-weight: 600;
        color: $text-extra;

        img {
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            z-index: -2;
        }

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: $types-overlay;
            z-index: -1;
            transition: .2s linear;
        }

        &:hover::before {
            opacity: 0;
        }
    }
}
