.help {
    @include container;

    &__content {
        width: 100%;
        background: $main-gradient;
        border-radius: 30px;
        padding: 48px 48px 72px;
        position: relative;

        img {
            position: absolute;
            right: 42px;
            bottom: 26px;
        }
    }

    &__title {
        color: $text-extra;
        font-family: "Ubuntu";
        font-size: 48px;
        font-weight: 500;
    }

    &__text {
        margin-block: 32px 48px;
        font-family: "Comfortaa";
        font-size: 32px;
        font-weight: 600;
        line-height: 125%;
        color: $text-extra;
    }

    &__form {
        display: flex;
        align-items: center;

        *:not(:last-child) {
            margin-right: 32px;
        }

        button {
            @extend %form-btn;
        }

        input {
            @extend %form-input;

            width: 304px;
        }
    }
}
