.header {
    background: $nav-bg;

    &__promo {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 16px;
        position: relative;
        z-index: 1;
        background: $promo-gradient;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background: $promo-gradient;
            z-index: -1;
            transition: width 0.6s linear;
        }

        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 0;
            height: 100%;
            background: $promo-gradient_hover;
            z-index: -1;
            transition: width 0.6s linear;
        }

        &:hover {
            &::after {
                width: 100%;
            }

            &::before {
                width: 0;
            }
        }

        &-icon {
            font-size: 34px;
            margin-right: 16px;
            transition: color 0.2s;
        }

        &:hover &-icon::before {
            color: $text-extra_hover;
        }

        &-text {
            color: $text-extra;
            font-family: "Montserrat";
            font-size: 22px;
            font-weight: 500;
            transition: color 0.2s;
        }

        &:hover &-text {
            color: $text-extra_hover;
        }
    }

    &__upperNav {
        @include container;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-block: 16px;

        border-bottom: 2px solid $text_hover;

        &-tel {
            display: flex;
            align-items: center;
            justify-content: center;

            color: $text;
            font-family: "Montserrat";
            font-size: 20px;
            font-weight: 400;

            transition: 0.2s linear;

            &:hover {
                color: $text_hover;
            }

            &:hover *::before {
                color: $text_hover;
            }
        }

        &-icon {
            font-size: 26px;
            margin-right: 12px;
        }

        &-socials {
            display: flex;
            align-items: center;
            justify-content: center;

            a {
                font-size: 40px;
                transition: 0.2s;

                &:not(:last-of-type) {
                    margin-right: 32px;
                }

                &:hover {
                    transform: scale(0.9);
                }
            }
        }

        &-lang {
            a {
                display: flex;
                align-items: center;
                justify-content: center;

                font-family: "Montserrat";
                font-size: 22px;
                font-weight: 500;
                text-transform: uppercase;
                color: $text;

                transition: 0.2s;

                &:hover {
                    color: $text_hover;
                }

                img {
                    width: 55px;
                    height: 38px;
                    margin-right: 16px;
                    border-radius: 6px;
                }

                &.hide {
                    display: none;
                }
            }
        }
    }

    &__lowerNav {
        @include container;
        padding-block: 16px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        &-logo {
            img {
                width: 208px;
                height: auto;
                object-fit: contain;
            }
        }

        &-list {
            display: flex;
            align-items: center;
        }

        &-item {
            &:not(:last-of-type) {
                margin-right: 32px;
            }
        }

        &-link {
            font-family: "Ubuntu";
            font-size: 22px;
            font-weight: 400;
            color: $text;
            transition: .2s;

            &:hover {
                color: $text_hover;
            }

            &.active {
                color: $text_active;
            }
        }
    }
}
