.stats {
    background: url("../images/backgrounds/stats.png") no-repeat center / cover;
    position: relative;
    z-index: 1;

    &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: $stats-overlay;
        z-index: -1;
    }

    &__content {
        @include container;
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding-block: 80px;
    }

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        &-img {
            width: 96px;
            height: 96px;
        }

        &-ratio {
            font-family: "Ubuntu";
            font-size: 48px;
            font-weight: 500;
            color: $text-extra;
            margin-block: 16px;
        }

        &-name {
            font-family: "Ubuntu";
            font-size: 22px;
            font-weight: 400;
            color: $text-extra;
        }
    }
}
