.airlines__slider {
    padding-block: 64px;
    background: $nav-bg;
    overflow: hidden;
    display: flex;
    align-items: center;



    &-content {
        display: flex;
        align-items: center;

        animation: slide 20s linear infinite;

        img {
            width: 230px;
            height: 60px;
            object-fit: contain;
            margin-right: 48px;
        }

        @keyframes slide {
            0% {
                transform: translateX(0);
            }
            100% {
                transform: translateX(-100%);
            }
        }
    }
}
