.gallery {
    @include container;

    padding-block: 72px;

    &__grid {
        margin-block: 48px 32px;

        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 30px;
    }

    &__photo {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;

        &:hover img {
            filter: grayscale(60%);
        }

        img {
            width: 100%;
            height: 100%;
            transition: 0.2s ease-in;
        }
    }

    &__photo:hover &__overlay {
        visibility: visible;
        opacity: 1;
    }

    &__overlay {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: .2s linear;
        visibility: hidden;
        opacity: 0;

        &-icon {
            font-size: 52px;
            color: $text;
            cursor: pointer;

            transition: .2s;

            &:hover {
                transform: scale(1.07);
            }
        }

        &-country {
            position: absolute;
            left: 24px;
            bottom: 24px;
            color: $text-extra;
            font-family: "Comfortaa";
            font-size: 24px;
            font-weight: 600;
        }
    }

    &__button {
        @extend %button;
    }
}
