.header {
  background: #eeeeee;
}
.header__promo {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px;
  position: relative;
  z-index: 1;
  background: linear-gradient(90deg, #6B59C1 0%, #5D2CCA 31%, #8F0ACE 62%, #755DC1 100%);
}
.header__promo::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #6B59C1 0%, #5D2CCA 31%, #8F0ACE 62%, #755DC1 100%);
  z-index: -1;
  transition: width 0.6s linear;
}
.header__promo::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: linear-gradient(90deg, #8F0ACE 15%, #5D2CCA 33%, #755DC1 68%, #6B59C1 100%);
  z-index: -1;
  transition: width 0.6s linear;
}
.header__promo:hover::after {
  width: 100%;
}
.header__promo:hover::before {
  width: 0;
}
.header__promo-icon {
  font-size: 34px;
  margin-right: 16px;
  transition: color 0.2s;
}
.header__promo:hover .header__promo-icon::before {
  color: rgba(255, 255, 255, 0.8156862745);
}
.header__promo-text {
  color: #FFFFFF;
  font-family: "Montserrat";
  font-size: 22px;
  font-weight: 500;
  transition: color 0.2s;
}
.header__promo:hover .header__promo-text {
  color: rgba(255, 255, 255, 0.8156862745);
}
