.gallery__grid {
  margin-block: 48px 32px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}
.gallery__photo {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.gallery__photo:hover img {
  filter: grayscale(60%);
}
.gallery__photo img {
  width: 100%;
  height: 100%;
  transition: 0.2s ease-in;
}
.gallery__photo:hover .gallery__overlay {
  visibility: visible;
  opacity: 1;
}
.gallery__overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.2s linear;
  visibility: hidden;
  opacity: 0;
}
.gallery__overlay-icon {
  font-size: 52px;
  color: #292929;
  cursor: pointer;
  transition: 0.2s;
}
.gallery__overlay-icon:hover {
  transform: scale(1.07);
}
.gallery__overlay-country {
  position: absolute;
  left: 24px;
  bottom: 24px;
  color: #FFFFFF;
  font-family: "Comfortaa";
  font-size: 24px;
  font-weight: 600;
}
.airlines__slider {
  padding-block: 64px;
  background: #eeeeee;
  overflow: hidden;
  display: flex;
  align-items: center;
}
.airlines__slider-content {
  display: flex;
  align-items: center;
  animation: slide 20s linear infinite;
}
.airlines__slider-content img {
  width: 230px;
  height: 60px;
  object-fit: contain;
  margin-right: 48px;
}
@keyframes slide {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

