* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

button {
    border: none;
    outline: none;
}

input {
    border: none;
    outline: none;
}

.wrapper {
    background: $main;
    font-family: "Ubuntu";
    *::before,
    *::after {
        font-family: "FontAwesome";
    }
    z-index: 1;
}

.icon {
    &-film::before {
        content: "\f008";
        color: $text-extra;
        font-weight: 300;
        transition: 0.2s;
    }

    &-tel::before {
        content: "\f879";
        color: $text;
        font-weight: 300;
        transition: 0.2s;
    }

    &-youtube::before {
        content: "\f167";
        background: $main-gradient;
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-weight: 400;
    }

    &-instagram::before {
        content: "\f16d";
        background: $main-gradient;
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-weight: 400;
    }

    &-telegram::before {
        content: "\f2c6";
        background: $main-gradient;
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-weight: 400;
    }

    &-scope::before {
        content: "\f065";
        color: $text-extra;
        font-weight: 300;
    }
}

.section {
    &-subtitle {
        color: $extra;
        font-family: "Comfortaa";
        font-size: 28px;
        font-weight: 600;
        margin-bottom: 12px;
    }

    &-title {
        color: $text;
        font-family: "Ubuntu";
        font-size: 48px;
        font-weight: 500;
        display: flex;
        flex-direction: column;
    }
}

%form {
    &-btn {
        color: $text-extra;
        font-family: "Comfortaa";
        font-size: 24px;
        font-weight: 600;
        padding: 16px 24px;

        border: 1.5px solid $text-extra;
        background: $extra;
        border-radius: 12px;
        cursor: pointer;
        transition: 0.2s linear;

        &:hover {
            background: #8d75ff;

            color: $text-extra_hover;
            border-color: $text-extra;
        }
    }

    &-input {
        padding: 16px 24px;
        border-radius: 10px;
        background: $text-extra;

        font-family: "Comfortaa";
        font-size: 24px;
        font-weight: 600;
        color: $text_hover;

        border: 2px solid transparent;
        transition: 0.2s linear;

        &:focus {
            border-color: $text_active;
        }
    }
}

%button {
    padding: 16px 40px;
    background: $light-button;
    color: $text-extra;
    font-family: "Ubuntu";
    font-size: 32px;
    font-weight: 500;
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: fit-content;
    margin: 0 auto;

    transition: .2s linear;

    &:hover {
        opacity: .8;
    }
}
